






















import Vue from "vue";
import * as tf from "@tensorflow/tfjs";
import DigitInput from '../../components/DigitInput.vue';
import model1url from '../../assets/models/minst1/model.json'
import model2url from '../../assets/models/minst2/model.json'

export default Vue.extend({
  data: () => {
    return {
      model: <tf.LayersModel | null>null,
      modelurl: "",
      isloadmodel: false,
      info: "",
      digit: "",
      options: [
        {
          label: "Model1",
          url: model1url
        },
        {
          label: "Model2",
          url: model2url
        }
      ]
    };
  },
  watch:{
    async modelurl(newval, oldval){
      if(newval == "") return;
      if(newval != oldval){
        this.isloadmodel = true;
        try{
          this.info = "";
          this.model = await tf.loadLayersModel(this.modelurl);
          this.model.summary(undefined, undefined, msg => {
            this.info = this.info + msg + "\n";
          });
        }catch(e){
          this.modelurl = "";
          this.$message({
            showClose: true,
            message: "Load Model Failed!!!"
          });
        }finally{
          this.isloadmodel = false;
        }
      }
    }
  },
  async mounted() {
    // this.info = "";
    // console.log(require('file-loader!../assets/models/minst1/group1-shard1of1.bin'));
    // console.log(require('file-loader!../assets/models/minst1/model.json'));
    // console.log(modelpath)
    // let modelurl = location.origin + <string>require('../assets/models/minst1/model.json')
    /*let modelurl = modelpath;
    try{
      let model = await tf.loadLayersModel(modelurl);
      this.model = model;
      model.summary(undefined, undefined, msg => {
        this.info = this.info + msg + "\n";
      });
    }catch(e){
      console.log('Load Model Failed!!!');
    }*/
  },
  components:{
    DigitInput
  }
})
