

























import Vue from "vue";
import * as tf from "@tensorflow/tfjs";
import model1url from '../../assets/models/gan1/model.json'
export default Vue.extend({
  data() {
    return {
      model: <tf.LayersModel | null>null,
      info: "",
      modelurl: "",
      isloadmodel: false,
      isgenimage: false,
      options: [
        {
          label: "Model1",
          url: model1url
        }
      ]
    };
  },
  watch:{
    async modelurl(newval, oldval){
      if(newval == "") return;
      if(newval != oldval){
        this.isloadmodel = true;
        try{
          this.info = "";
          this.model = await tf.loadLayersModel(this.modelurl);
          this.model.summary(undefined, undefined, msg => {
            this.info = this.info + msg + "\n";
          });
        }catch(e){
          this.modelurl = "";
          this.$message({
            showClose: true,
            message: "Load Model Failed!!!"
          });
        }finally{
          this.isloadmodel = false;
        }
      }
    }
  },
  async mounted(){
    /*this.info = "";
    let model = await tf.loadLayersModel(model1url);
    try{
      this.model = model;
      model.summary(undefined, undefined, msg => {
        this.info = this.info + msg + "\n";
      });
    }catch(e){
      console.log('Load Model Failed!!!');
    }*/
  },
  methods: {
    async generateImage(){
      let model = <tf.LayersModel>this.model;
      if(model == null){
        this.$message({
        showClose: true,
        message: "No Model Loaded!!!"
        });
        return;
      }
      this.isgenimage = true;
      let noise_tensor = this.generateNoises(25);
      let images_tensor =<tf.Tensor<tf.Rank>> model.predict(noise_tensor);
      images_tensor = images_tensor.add(1).mul(0.5);
      let images = await images_tensor.array();
      this.drawImages(images);
      this.isgenimage = false;
    },
    drawImages(images: [[[number]]]){
      let canvas = <HTMLCanvasElement>document.getElementById('res');
      if(canvas == null){
        console.log('canvas error');
        return;
      }
      let ctx = canvas.getContext('2d');
      ctx.fillRect(0, 0, 140, 140);
      let index = 0;
      for(let image of images){
        let xoff = Math.floor(index / 5) * 28;
        let yoff = index % 5 * 28;
        let y = 0;
        for(let i of image){
          let x = 0;
          for(let j of i){
            let data = ctx.getImageData(x+xoff, y+yoff, 1, 1);
            data.data[0] = j * 255;
            data.data[1] = j * 255;
            data.data[2] = j * 255;
            data.data[3] = 255;
            ctx.putImageData(data, x+xoff, y+yoff);
            ++x;
          }
          ++y;
        }
        ++index;
      }
    },
    generateNormalDis(mu: number, sigma: number, size: number){
      let res = [];
      for(let i = 0; i < size; ++i){
        let u = Math.random();
        let v = Math.random();
        let z = Math.sqrt(-2 * Math.log(u)) * Math.cos(2*Math.PI*v);
        res.push(z*sigma + mu);
      }
      return res;
    },
    generateNoises(size: number){
      let res = []
      for(let i = 0; i < size; ++i){
        res.push(this.generateNormalDis(0, 1, 100));
      }
      return tf.tensor2d(res);
    }
  }
});
