import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/tensorflow/minst1',
    component: () => import('../views/Tensorflow/Minst1.vue')
  },
  {
    path: '/tensorflow/minst2',
    component: () => import('../views/Tensorflow/Minst2.vue')
  },
  {
    path: '/tensorflow/gan1',
    component: () => import('../views/Tensorflow/Gan1.vue')
  },
  {
    path: '/3d/stencil',
    component: () => import('../views/3D/Stencil.vue')
  },
  {
    path: '/3d/skybox',
    component: () => import('../views/3D/SkyBox.vue')
  },
  {
    path: '/3d/reflector',
    component: () => import('../views/3D/Reflector.vue')
  },
  {
    path: '/3d/water',
    component: () => import('../views/3D/Water.vue')
  },
  {
    path: '/tune',
    component: () => import('../views/Tune.vue')
  },
  {
    path: '/gerberview',
    component: () => import('../views/GerberView.vue')
  },
  {
    path: '/tensorflow/styletransfer1',
    component: () => import('../views/Tensorflow/StyleTransfer1.vue')
  },
  {
    path: '/bspline',
    component: () => import('../views/BSpline.vue')
  },
  {
    path: '/record',
    component: () => import('../views/Record.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
