






import Vue from "vue";
import { Tune } from "../Tune";
import echarts from 'echarts';
export default Vue.extend({
  methods: {
    animation() {
      let res: [string, number];
      if(Date.now() - this.interval > 50){
        res = this.tune.getName();
        this.res = res;
        this.interval = Date.now();
      }else
      {
        res = this.res;
      }
      let showelt = <HTMLCanvasElement>document.getElementById("show");
      if(!showelt) return;
      let ctx = showelt.getContext("2d");
      ctx.clearRect(0, 0, showelt.width, showelt.height);
      if(res){
        ctx.fillStyle=`rgb(0, ${150 - 50 * Math.abs(res[1])}, 0)`;
        ctx.fillRect(0, 0, showelt.width, showelt.height);
      }
      ctx.fillStyle = '#333333';
      ctx.beginPath();
      ctx.lineWidth = 3;
      ctx.moveTo(0, showelt.height);
      ctx.lineTo(showelt.width, showelt.height);
      ctx.stroke();
      ctx.beginPath();
      ctx.lineWidth = 2;
      ctx.moveTo(showelt.width * 0.5, showelt.height * 0.8);
      ctx.lineTo(showelt.width * 0.5, showelt.height);
      ctx.stroke();
      if (res) {
        ctx.font = `${showelt.height * 0.5}px Consolas`;
        ctx.fillText(res[0], showelt.height*0.36, showelt.height * 0.7);
        ctx.beginPath();
        ctx.lineWidth = 1;
        ctx.moveTo(
          showelt.width * 0.5 + showelt.width * 0.5 * res[1],
          showelt.height * 0.9
        );
        ctx.lineTo(
          showelt.width * 0.5 + showelt.width * 0.5 * res[1],
          showelt.height
        );
        ctx.stroke();
      }
      if(this.stop == false) requestAnimationFrame(this.animation);
    }
  },
  data() {
    return {
      interval: Date.now(),
      res: ['', 0],
      tune: <Tune|null>null,
      stop: false
    };
  },
  async mounted() {
    try{
      let stream = await navigator.mediaDevices.getUserMedia({
        audio: true,
        video: false
      });
      this.tune = new Tune(stream);
      let gauge = echarts.init(this.$refs.res);
      gauge.setOption({
        series: [
          {
            type: 'gauge',
            axisLine:{
              show: false
            },
            axisLabel:{
              show: false
            },
            splitLine:{
              lineStyle:{
                color: 'black'
              }
            },
            title:{
              show: true,
              offsetCenter: [0, '40%'],
              // fontWeight: 'bold',
              fontSize: 50
            },
            detail:{
              show: false
            },
            itemStyle:{
              color: '#5e5e5e',
            },
            min: -1,
            max: 1,
            markPoint:{
              animation: false
            },
            data: [
              {
                name: 'C#',
                value: 5
              }
            ]
          }
        ]
      })
      let update = ()=>{
        let val = this.tune.getName();
        if(val){
          gauge.setOption({
            series:[
              {
                data:[
                  {
                    name: val[0],
                    value: val[1]
                  }
                ]
              }
            ]
          });
        }else{
          gauge.setOption({
            series:[
              {
                data:[
                  {
                    name: '',
                    value: 0
                  }
                ]
              }
            ]
          });
        }
        if(!this.stop){
          setTimeout(update, 500);
        }
      };
      update();
      // requestAnimationFrame(this.animation);
    }catch(e){
      this.$message({
        showClose: true,
        message: e.message
        });
    }
  },
  destroyed() {
    this.stop = true;
  }
});
