
export class Tune {
    audioCtx: AudioContext;
    analyse_node: AnalyserNode;
    rel: Map<number, string> = new Map<number, string>();
    constructor(pStream: MediaStream) {
        let audioCtx = new AudioContext();
        this.audioCtx = audioCtx;
        let n1 = this.audioCtx.createMediaStreamSource(pStream);
        let n2 = this.audioCtx.createAnalyser();
        n2.fftSize = Math.pow(2, 15);
        n1.connect(n2);
        this.analyse_node = n2;
        let rel = this.rel;
        rel.set(261.63, 'C');
        rel.set(277.18, 'C#');
        rel.set(293.66, 'D');
        rel.set(311.13, 'D#');
        rel.set(329.63, 'E');
        rel.set(349.23, 'F');
        rel.set(369.99, 'F#');
        rel.set(392.00, 'G');
        rel.set(415.30, 'G#');
        rel.set(440.00, 'A');
        rel.set(466.16, 'A#');
        rel.set(493.88, 'B');
        rel.set(523.25, 'C');
    }
    getFrequence(): number|null {
        let farray = new Uint8Array(this.analyse_node.frequencyBinCount);
        this.analyse_node.getByteFrequencyData(farray);
        let maxindex = 0;
        farray.forEach((value, index) => {
            if (value > farray[maxindex]) {
                maxindex = index;
            }
        });
        if (farray[maxindex] < 150) {
            return null;
        }
        return maxindex * this.audioCtx.sampleRate / this.analyse_node.fftSize
    }
    getName(): [string, number]|null {
        let fre = this.getFrequence();
        let dis;
        if (fre) {
            while(fre < 261.63){
                fre = fre * 2.0;
            }
            while(fre > 523.25){
                fre = fre * 0.5;
            }
            let rel = this.rel;
            let name: string = ""
            let left = 261.63;
            let right = 277.18;
            for (let [key] of rel) {
                if(fre > key){
                    left = key;
                }
                if(fre < key){
                    right = key;
                    break;
                }
            }
            let center = (right + left)*0.5;
            if(fre > center){
                name = rel.get(right)!;
                dis = (fre - right) / ((right - left) * 0.5);
            }else{
                name = rel.get(left)!;
                dis = (fre - left) / ((right - left) * 0.5);
            }
            return [name, dis];
        }
        return null;
    }
}
