export class BSpline{
    private _mode = -1;
    private _rawpoints: [number, number][] = [];
    private _points: [number, number][] = [];
    // point, axes
    private _nodes: number[] = [];
    private _k = 1;
    public addPoint(point: [number, number]){
      this._rawpoints.push(point);
      this.generateNodes();
    }
    public removePoint(index: number){
      this._rawpoints.splice(index, 1);
      this.generateNodes();
    }
    set Mode(val: number){
      this._mode = val;
      this.generateNodes();
    }
    set K(val: number){
      this._k = val;
      this.generateNodes();
    }
    get Points(){
      return this._rawpoints;
    }
    clear(){
     this._rawpoints = [];
     this.generateNodes(); 
    }
    private generateNodes(){
      this._nodes = [];
      switch(this._mode){
        case 0:
          for(let i = 0; i < this._rawpoints.length+this._k+1; ++i){
            this._nodes.push(i);
            this._points = this._rawpoints.slice();
          }
          break;
        case 1:
          for(let i = 0; i < this._rawpoints.length+this._k+1; ++i){
            this._nodes.push(i);
          }
          for(let i = 0; i < this._k; ++i){
            this._nodes[i]  = this._nodes[this._k];
          }
          for(let i = 0; i < this._k; ++i){
            this._nodes[this._nodes.length-1-i] = this._nodes[this._nodes.length-this._k-1];
          }
          this._points = this._rawpoints.slice();
          break;
        case 2:
          if(this._rawpoints.length < 3){
            return;
          }
          this._points = this._rawpoints.slice();
          this._points = this._points.concat(this._points);
          for(let i = 0; i < this._points.length+this._k+1; ++i){
            this._nodes.push(i);
          }
          break;
        default:
          break;
      }
    }
    public getPoint(x: number): [number, number]|null{
      if(this._k >= this._rawpoints.length){
        return null;
      }
      if(this._mode == -1){
        return null;
      }
      if(x < 0 || x > 1){
        return null;
      }
      let u = 0;
      switch(this._mode){
        case 0:
          u = this._k + x * (this._nodes.length - 2*this._k - 1)
          break;
        case 1:
          u = this._k + x * (this._nodes.length - 2*this._k - 1);
          break;
        case 2:
          u = this._k + x * (this._nodes.length - 2*this._k - 1);
          break;
      }
      let res: [number, number] = [0, 0]
      for(let i = 0; i < this._points.length; ++i){
        let k = this.getValue(i, this._k, u);
        res[0] += k*this._points[i][0];
        res[1] += k*this._points[i][1];
      }
      return res;
    }
    private getValue(i: number, k: number, x: number): number{
      if(k == 0){
        if(this._nodes[i] === this._nodes[i+1]){
          if(x === this._nodes[i])
            return 1;
        }
        if(x >= this._nodes[i] && x < this._nodes[i+1]){
          return 1;
        }
        return 0;
      }
      let first = 0;
      if(this._nodes[i+k]-this._nodes[i] == 0){
        first = this.getValue(i, k-1, x);
      }else{
        first = (x - this._nodes[i])/(this._nodes[i+k]-this._nodes[i])*this.getValue(i, k-1, x);
      }
      if(first === 1){
        return first;
      }
      let second = 0;
      if(this._nodes[i+k+1]-this._nodes[i+1] == 0){
        second = this.getValue(i+1, k-1, x);
      }else{
        second = (this._nodes[i+k+1]-x)/(this._nodes[i+k+1]-this._nodes[i+1])*this.getValue(i+1, k-1, x);
      }
      return first + second;
    }
  }