




























import Vue from 'vue'
import {BSpline} from '../BSpline';

export default Vue.extend({
  data(){
    return{
      splinemode: <number|string>"",
      ctx: <CanvasRenderingContext2D>{},
      width: 7,
      bspline: new BSpline(),
      k: 1,
      point: <[number, number]|null>null,
      stop: false
    }
  },
  watch:{
    splinemode(val: number|string){
      if(val === ""){
        this.bspline.Mode = -1;
      }else{
        this.bspline.Mode = <number>val;
      }
    },
    k(val: number){
      this.bspline.K = val;
    }
  },
  mounted(){
    let paper = <HTMLCanvasElement>document.getElementById('paper');
    paper.onmousedown = (ev)=>{
      if(ev.buttons == 1){
        for(let point of this.bspline.Points){
          if(Math.pow(point[0]-ev.offsetX, 2) + Math.pow(point[1]-ev.offsetY, 2) < this.width*this.width){
            this.point = point
            break;
          }
        }
        if(this.point == null) this.bspline.addPoint([ev.offsetX, ev.offsetY]);
      }else{
        for(let i = 0; i < this.bspline.Points.length; ++i){
          let point = this.bspline.Points[i];
          if(Math.pow(point[0]-ev.offsetX, 2) + Math.pow(point[1]-ev.offsetY, 2) < this.width*this.width){
            this.bspline.removePoint(i);
            break;
          }
        }
      }
      ev.preventDefault();
    };
    paper.onmousemove = (ev)=>{
      if(this.point){
        this.point[0] = ev.offsetX;
        this.point[1] = ev.offsetY;
      }
      ev.preventDefault();
    }
    paper.onmouseup = (ev)=>{
      this.point = null;
    }
    paper.oncontextmenu = (ev)=>{
      ev.preventDefault();
    }
    this.ctx = paper.getContext('2d')!;
    this.animation();
  },
  methods:{
    animation(){
      this.ctx.clearRect(0, 0, 10000, 10000);
      for(let point of this.bspline.Points){
        this.ctx.fillStyle = 'red'
        this.ctx.beginPath();
        this.ctx.arc(point[0], point[1], this.width, 0, 2*Math.PI);
        this.ctx.fill();
        this.ctx.beginPath()
        let sample = 500;
        for(let i = 0; i <= sample; i++){
          let point = this.bspline.getPoint(i/sample)!;
          if(point == null){
            break;
          }
          if(i == 0){
            this.ctx.moveTo(point[0], point[1]);
          }else{
            this.ctx.lineTo(point[0], point[1]);
          }
        }
        // this.ctx.closePath();
        this.ctx.stroke();
      }
      if(this.stop) return
      requestAnimationFrame(this.animation)
    },
    clear(){
      this.bspline.clear();
    }
  },
  destroyed(){
    this.stop = true;
  }
})
