let s = `#define PHONG
varying vec3 vViewPosition;
uniform float time;
#ifndef FLAT_SHADED
	varying vec3 vNormal;
#endif
#include <common>
#include <uv_pars_vertex>
#include <uv2_pars_vertex>
#include <displacementmap_pars_vertex>
#include <envmap_pars_vertex>
#include <color_pars_vertex>
#include <fog_pars_vertex>
#include <morphtarget_pars_vertex>
#include <skinning_pars_vertex>
#include <shadowmap_pars_vertex>
#include <logdepthbuf_pars_vertex>
#include <clipping_planes_pars_vertex>
void main() {
	#include <uv_vertex>
	#include <uv2_vertex>
	#include <color_vertex>
	#include <beginnormal_vertex>
	#include <morphnormal_vertex>
	#include <skinbase_vertex>
	#include <skinnormal_vertex>
	#include <defaultnormal_vertex>
#ifndef FLAT_SHADED
	vNormal = normalize( transformedNormal );
#endif
	#include <begin_vertex>
	#include <morphtarget_vertex>
	#include <skinning_vertex>
	#include <displacementmap_vertex>
	#include <project_vertex>
	#include <logdepthbuf_vertex>
	#include <clipping_planes_vertex>
	vViewPosition = - mvPosition.xyz;
	#include <worldpos_vertex>
	#include <envmap_vertex>
	#include <shadowmap_vertex>
  #include <fog_vertex>
  // gl_Position.x = 1.0;
  // gl_Position.x = 0.0;
  gl_Position = projectionMatrix * modelViewMatrix * vec4( position.x, position.y, 5.0*sin((position.y + time)/5.0), 1.0 );
}`

export default Vue.extend({
  data() {
    return {
      renderer: WebGLRenderer,
      camera: PerspectiveCamera,
      scene: Scene,
      ended: false,
      custommat: ShaderMaterial
    };
  },
  mounted() {
    let renderer = new WebGLRenderer();
    renderer.autoClear = false;
    renderer.setSize(600, 600);
    renderer.setClearColor(new Color(0.6, 0.6, 0.6), 1);

    let dom = renderer.domElement;
    document.getElementById("root").appendChild(dom);

    let camera = new PerspectiveCamera(75, 1, 0.1, 1000);
    camera.position.z = 200;
    let controls = new OrbitControls(camera, renderer.domElement);
    controls.enableKeys = false;
    // controls.enableZoom = false;

    this.camera = camera;
    this.scene = this.initScene();
    this.renderer = renderer;
    this.animate();
  },
  methods: {
    initScene(): Scene {
      let scene = new Scene();

      let light = new DirectionalLight(0xffffff, 1.0);
      light.position.y = 100;
      light.position.z = 100;
      scene.add(light);

      scene.background = new CubeTextureLoader().load([
        pxurl,
        nxurl,
        pyurl,
        nyurl,
        pzurl,
        nzurl
      ]);

      // scene.add(new DirectionalLightHelper(light, 100));
      // let water = new Water(
      //   new PlaneBufferGeometry(1000, 1000), 
      //   {
      //     waterColor: 0x001e0f,
      //     distortionScale: 50.0,
      //     sunDirection: light.position.clone().normalize(),
      //     sunColor: light.color,
      //   }
      // );
      let ball = new Mesh(
        new SphereBufferGeometry(50, 30, 30),
        new MeshPhongMaterial({color: 0x898989})
      );
      // scene.add(ball);
      let water = new Water(
        new PlaneBufferGeometry(200, 200),
        {
          color: 0x6ec3ff,
          flowDirection: new Vector2(1, 1),
          normalMap0: new TextureLoader().load(texture1url),
          normalMap1: new TextureLoader().load(texture2url),
          textureWidth: 1000,
          textureHeight: 1000,
          scale: 0.5,
          reflectivity: 0.5,
          flowSpeed: 0.05
        }
      )
      // console.log(water.material);
      this.custommat = <ShaderMaterial>water.material;
      console.log(this.custommat.fragmentShader)
      // mat.uniforms.time.value = 10;

      water.rotateX(-Math.PI*0.5);
      water.position.y = -50;
      scene.add(water);

      // let uniforms = UniformsUtils.merge([
      //   UniformsLib.lights,
      //   {
      //     lights: UniformsLib.lights,
      //       emissive: { value: new Vector3(0.8, 0.8, 0.8)},
      //       // diffuse: { value: new Vector3(1, 1, 1)},
      //       specular: { value: new Vector3(1, 1, 1)},
      //       shininess: {value: 100},
      //       time: {value: Date.now() % Math.PI*5}
      //   }
      // ]);

      // let helper = new AxesHelper(10);
      // scene.add(helper);

      // let custommat = new ShaderMaterial({
      //     side: DoubleSide,
      //     lights: true,
      //     // uniforms: WaterRefractionShader.uniforms,
      //     wireframe: false,
      //     // vertexShader: s,
      //     // fragmentShader: ShaderLib.phong.fragmentShader
      //     vertexShader: WaterRefractionShader.vertexShader,
      //     fragmentShader: WaterRefractionShader.fragmentShader
      //   });
      // let flag = new Mesh(
      //   new PlaneBufferGeometry(200, 200, 100, 100),
      //   custommat
      // )
      // flag.rotateX(-Math.PI*0.5);
      // // flag.position.y = -100;

      // this.custommat = custommat;
      // // console.log(ShaderLib.phong.vertexShader);
      // scene.add(flag);

      return scene;
    },

    animate() {
      let renderer = <WebGLRenderer>this.renderer;

      renderer.clear();

      // this.custommat.uniforms.time.value = Date.now()/1000;

      renderer.render(this.scene, this.camera);

      if (!this.ended) {
        requestAnimationFrame(this.animate);
      }
    }
  },

  destroyed() {
    this.ended = true;
  }
});
