



import Vue from "vue";
import {
  Matrix3,
  WebGLRenderer,
  PerspectiveCamera,
  Scene,
  BoxGeometry,
  CylinderGeometry,
  MeshLambertMaterial,
  Color,
  AmbientLight,
  Mesh,
  BackSide,
  OrthographicCamera,
  DirectionalLight,
  MeshPhongMaterial,
  SphereBufferGeometry,
  CubeTextureLoader,
  CubeCamera,
  PlaneBufferGeometry
} from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { Reflector } from 'three/examples/jsm/objects/Reflector';
import { Refractor } from 'three/examples/jsm/objects/Refractor';
import { WaterRefractionShader } from 'three/examples/jsm/shaders/WaterRefractionShader';
import nxurl from "../../assets/img/negx.jpg";
import pxurl from "../../assets/img/posx.jpg";
import nyurl from "../../assets/img/negy.jpg";
import pyurl from "../../assets/img/posy.jpg";
import nzurl from "../../assets/img/negz.jpg";
import pzurl from "../../assets/img/posz.jpg";

export default Vue.extend({
  data() {
    return {
      renderer: WebGLRenderer,
      camera: PerspectiveCamera,
      scene: Scene,
      ended: false
    };
  },
  mounted() {
    let renderer = new WebGLRenderer();
    renderer.autoClear = false;
    renderer.setSize(600, 600);
    renderer.setClearColor(new Color(0.6, 0.6, 0.6), 1);

    let dom = renderer.domElement;
    document.getElementById("root").appendChild(dom);

    let camera = new PerspectiveCamera(75, 1, 0.1, 1000);
    camera.position.z = 200;
    let controls = new OrbitControls(camera, renderer.domElement);
    controls.enableKeys = false;
    controls.enableZoom = false;

    this.camera = camera;
    this.scene = this.initScene();
    this.renderer = renderer;
    this.animate();
  },
  methods: {
    initScene(): Scene {
      let scene = new Scene();

      scene.background = new CubeTextureLoader().load([
        pxurl,
        nxurl,
        pyurl,
        nyurl,
        pzurl,
        nzurl
      ]);

      let mirror = new Reflector(
        new PlaneBufferGeometry(400, 400)
      );

      mirror.rotateX(-Math.PI * 0.5);
      mirror.position.y = -50;

      scene.add(mirror);

      let mirror2 = new Reflector(
        new PlaneBufferGeometry(400, 400)
      );

      mirror2.rotateX(Math.PI * 0.5);
      mirror2.position.y = -50.1;

      scene.add(mirror2);

      let light = new DirectionalLight(0xffffff, 1.0);;
      light.position.y = 100;
      light.position.z = 100;
      scene.add(light);

      return scene;
    },

    animate() {
      let renderer = <WebGLRenderer>this.renderer;

      renderer.clear();

      renderer.render(this.scene, this.camera);

      if (!this.ended) {
        requestAnimationFrame(this.animate);
      }
    }
  },

  destroyed() {
    this.ended = true;
  }
});
