

























import Vue from "vue";
import * as tfjs from "@tensorflow/tfjs";
import wavemodelurl from "../../assets/models/wave/model.json";
import udniemodelurl from "../../assets/models/udnie/model.json";
import birdmodelurl from "../../assets/models/bird/model.json";

export default Vue.extend({
  data() {
    return {
      model: tfjs.LayersModel,
      isupload: false,
      isselectstyle: false,
      modelurl: "",
      isloadmodel: false,
      isgenerate: false,
      options: [
        {
          url: wavemodelurl,
          label: "Wave"
        },
        {
          url: udniemodelurl,
          label: "Udnie"
        },
        {
          url: birdmodelurl,
          label: "Bird"
        }
      ]
    };
  },
  watch: {
    async modelurl(newval, oldval) {
      if (newval == "") return;
      if (newval != oldval) {
        this.isloadmodel = true;
        try {
          this.model = await tfjs.loadLayersModel(this.modelurl);
          this.isselectstyle = true;
        } catch (e) {
          this.isselectstyle = false;
          this.modelurl = "";
          this.$message({
            showClose: true,
            message: "Load Model Failed!!!"
          });
        } finally {
          this.isloadmodel = false;
        }
      }
    }
  },
  methods: {
    choosesize(width: number, height: number): [number, number]{
      let ratios = [1, 4/3, 16/9];
      let ratio = width / height;
      let nearest = Math.abs(ratios[0] - ratio);
      let select = ratios[0];
      for(let it of ratios) {
        let curnearest = Math.abs(it - ratio);
        if(curnearest < nearest){
          select = it;
        }
      }
      if(select == 16/9){
        return [400, 400/select];
      }else if(select == 4/3){
        return [300, 300/select];
      }else
        return [300, 300];
    },
    uploadimage(file: File) {
      let incanvas = <HTMLCanvasElement>document.getElementById("orginimg");
      let outcanvas = <HTMLCanvasElement>document.getElementById("processimg");
      let reader = new FileReader();
      let image = new Image();
      this.isupload = true;
      image.src = URL.createObjectURL(file);
      image.onload = () => {
        let size = this.choosesize(image.width, image.height);
        incanvas.width = size[0];
        incanvas.height = size[1];
        outcanvas.width = size[0];
        outcanvas.height = size[1];
        let ctx = incanvas.getContext("2d");
        ctx.drawImage(image, 0, 0, image.width, image.height, 0, 0, size[0], size[1]);
        this.isupload = false;
      };
    },
    getinputtensor() {
      let canvas = <HTMLCanvasElement>document.getElementById("orginimg");
      let ctx = canvas.getContext("2d");
      let imgdata = [];
      for (let i = 0; i < canvas.height; ++i) {
        imgdata.push([]);
        for (let j = 0; j < canvas.width; ++j) {
          imgdata[i].push([]);
          imgdata[i][j].push([255, 255, 255]);
          let pixel = ctx.getImageData(j, i, 1, 1);
          imgdata[i][j][0] = pixel.data[0];
          imgdata[i][j][1] = pixel.data[1];
          imgdata[i][j][2] = pixel.data[2];
        }
      }
      return tfjs.tensor(
        [imgdata],
        [1, canvas.height, canvas.width, 3],
        "float32"
      );
    },
    async generatorimage() {
      if (this.model) {
        this.isgenerate = true;
        try {
          let input = this.getinputtensor();
          let out = this.model.predict(input);
          let images = await out.array();
          let outcanvas = <HTMLCanvasElement>(
            document.getElementById("processimg")
          );
          let ctx = outcanvas.getContext("2d");
          ctx.fillRect(0, 0, outcanvas.width, outcanvas.height);
          for (let image of images) {
            let y = 0;
            for (let row of image) {
              let x = 0;
              for (let pixel of row) {
                let data = ctx.getImageData(x, y, 1, 1);
                data.data[0] = (pixel[0] + 1) * 0.5 * 255;
                data.data[1] = (pixel[1] + 1) * 0.5 * 255;
                data.data[2] = (pixel[2] + 1) * 0.5 * 255;
                data.data[3] = 255;
                ctx.putImageData(data, x, y);
                ++x;
              }
              ++y;
            }
          }
        } catch (e) {
          console.log(e);
        }
        finally{
          this.isgenerate = false;
        }
      } else {
        this.$message({
            showClose: true,
            message: "Generator Error!!!"
          });
      }
    }
  }
});
