
























import Vue from "vue";
import Component from "vue-class-component";
declare var MediaRecorder: any;
@Component
export default class Test extends Vue {
  is_recording: boolean = false;
  media_stream: MediaStream = null;
  // @ts-ignore
  media_record: MediaRecorder = null;
  stop_on_start: Boolean = true;
  $refs!: {
    preview: HTMLVideoElement;
  };

  mounted() {}

  async UI_getMediaDeviceStream() {
    if (await this.UI_getIsRecording()) {
      return;
    }
    try {
      if (this.media_stream && this.media_stream.active) {
        this.media_stream.getTracks().forEach((track) => track.stop());
      }
      this.media_stream = await navigator.mediaDevices.getUserMedia({
        audio: true,
        video: true,
      });
      this.$refs.preview.srcObject = this.media_stream;
    } catch (err) {
      console.error(err);
      this.$message({
        message: err,
        type: "error",
      });
    }
  }

  async UI_getCaptureScreen() {
    if (await this.UI_getIsRecording()) {
      return;
    }
    try {
      if (this.media_stream && this.media_stream.active) {
        this.media_stream.getTracks().forEach((track) => track.stop());
      }
      // @ts-ignore
      this.media_stream = await navigator.mediaDevices.getDisplayMedia({
        audio: true,
        video: true,
      });
      this.$refs.preview.srcObject = this.media_stream;
    } catch (err) {
      console.error(err);
      this.$message({
        message: err,
        type: "error",
      });
    }
  }

  async UI_getIsRecording(): Promise<boolean> {
    if (this.is_recording) {
      this.$message({
        message: "Stop Record First",
        type: "info",
      });
    }
    return this.is_recording;
  }

  async UI_startRecord() {
    if (await this.UI_getIsRecording()) {
      return;
    }
    try {
      if (this.media_stream === null || this.media_stream.active === false) {
        throw new Error("CaptureScreen or MediaDevice First");
      }
      if (this.stop_on_start) {
        this.$refs.preview.pause();
      }
      this.media_record = new MediaRecorder(this.media_stream);
      this.media_record.ondataavailable = (event) => {
        if (this.stop_on_start) {
          if (this.media_stream && this.media_stream.active) {
            this.$refs.preview.play();
          } else {
            this.$refs.preview.srcObject = null;
          }
        }
        this.is_recording = false;
        console.log(" Recorded chunk of size " + event.data.size + "B");
        let blob: Blob = event.data;
        let url = URL.createObjectURL(blob);
        let a = document.createElement("a");
        document.body.append(a);
        a.href = url;
        a.type = blob.type;
        a.download = `record-${new Date().getTime()}`;
        a.click();
      };
      this.media_record.start();
      this.is_recording = true;
    } catch (err) {
      this.$message({
        message: err,
        type: "info",
      });
    }
  }

  async UI_stopRecord() {
    if (this.is_recording === false) {
      return;
    }
    try {
      this.media_record.stop();
    } catch (err) {
      console.error(err);
      this.$message({
        message: err,
        type: "error",
      });
    }
  }
}
